import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import 'react-mdl/extra/material.css';
import 'react-mdl/extra/material.js';
import { Router } from 'react-router-dom';

import { createBrowserHistory } from 'history';
import ReactGA from 'react-ga';

const trackingId = "UA-83942261-2";
ReactGA.initialize(trackingId);
ReactGA.pageview(window.location.pathname + window.location.search);

const history = createBrowserHistory();
history.listen(location => {
	console.log("location:" + location.pathname)
	ReactGA.pageview(location.pathname);
});

{
	let ip = "?";
	let location = "?";
	fetch(`http://www.geoplugin.net/json.gp`).then((response)=>{
		return response.json()
	}).then((json)=>{
		ip = json.geoplugin_request;
		location = `${json.geoplugin_city}, ${json.geoplugin_region}, ${json.geoplugin_countryName}`;
	}).catch(()=>{
	}).finally(()=>{
		ReactGA.set({ip:ip})
		ReactGA.set({location:location})
		ReactGA.event({
			category: "User",
			action: "Opened the website",
			label: `${ip} in ${location}`
		})
	})
}


ReactDOM.render(
	<Router history={history}>
		<App />
	</Router>, document.getElementById('root')
);

registerServiceWorker();
