import React, { Component } from 'react';
import publicIp from 'public-ip';

class Test extends Component {
    constructor(props) {
        super(props);
        this.state ={};
    }
    
    componentDidMount(){
        let ip = '?';
        let location = '?';

        fetch(`http://www.geoplugin.net/json.gp`).then((response)=>{
            return response.json()
        }).then((json)=>{
            ip = json.geoplugin_request;
            location = `${json.geoplugin_city}, ${json.geoplugin_region}, ${json.geoplugin_countryName}`;
            return (ip, location)
        }).catch((err)=>{
            console.log("error caught")
        }).finally(()=>{
            this.setState({
                ip:ip,
                location:location
            })
        })
        
    }

	render() {
        return <h1>
            This is a test.<br/>
            Your IP address is {this.state.ip}.<br/>
            You live in {this.state.location}.
        </h1>
	}
}

export default Test;
